import React, { useState } from 'react';
import DatePicker from 'react-datepicker';
import 'react-datepicker/dist/react-datepicker.css';
import './DateScroller.css';

const DateScroller = ({ selectedDate, onDateChange }) => {
  const [isCalendarOpen, setIsCalendarOpen] = useState(false);

  const dates = [];
  for (let i = -8; i <= 8; i++) {
    const date = new Date(selectedDate);
    date.setDate(selectedDate.getDate() + i);
    dates.push(date);
  }

  const formatDay = (date) => {
    return date.getDate();
  };

  const formatWeekday = (date) => {
    return date.toLocaleDateString('en-US', { weekday: 'short' });
  };

  const formatMonthYear = (date) => {
    return date.toLocaleDateString('en-US', { month: 'long', year: 'numeric' });
  };

  const handleMonthClick = () => {
    setIsCalendarOpen(!isCalendarOpen);
  };

  const handleDatePickerChange = (date) => {
    onDateChange(date);
    setIsCalendarOpen(false);
  };

  return (
    <div className="date-scroller-container">
      <div className="date-scroller">
        <button className="scroll-button left" onClick={() => onDateChange(new Date(selectedDate.setDate(selectedDate.getDate() - 1)))}>&lt;</button>
        <div className="dates-wrapper">
          <div className="month-year" onClick={handleMonthClick}>
            {formatMonthYear(selectedDate)}
          </div>
          {isCalendarOpen && (
            <div className="calendar-popup">
              <DatePicker
                selected={selectedDate}
                onChange={handleDatePickerChange}
                inline
              />
            </div>
          )}
          <div className="dates-container">
            {dates.map((date) => (
              <div
                key={date.toISOString()}
                className={`date-item ${date.toDateString() === selectedDate.toDateString() ? 'selected' : ''}`}
                onClick={() => onDateChange(date)}
              >
                <div className="weekday">{formatWeekday(date)}</div>
                <div className="day">{formatDay(date)}</div>
              </div>
            ))}
          </div>
        </div>
        <button className="scroll-button right" onClick={() => onDateChange(new Date(selectedDate.setDate(selectedDate.getDate() + 1)))}>&gt;</button>
      </div>
    </div>
  );
};

export default DateScroller;
